.breed-select {
  width: 200px;
  height: 30px;
  text-align: center;
  background-color: #bc8f8f;
  border: 2px solid #000;
  margin-bottom: 30px;
  font-size: 16px;
}

.cat-info {
  width: 500px;
}

.hidden {
  display: none;
}

.loader {
  color: #000;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 25px;
  font-weight: 400;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  height: 4px;
  width: 0%;
  background: #ff3d00;
  animation: lineGrow 5s linear infinite;
  display: block;
}

@keyframes lineGrow {
  to {
    width: 100%;
  }
}

